import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Form } from 'react-bootstrap';

export default function OwnerAdd() {

    let [name, setName] = useState("")
    let [surname, setSurname] = useState("")
    let [owner_description, setOwner_description] = useState("")
    let [owner_departament, setOwner_departament] = useState("")

    let [departament, setDepartament] = useState([])

    useEffect(() =>{
        axios({
        method: "GET",
        url: "http://127.0.0.1:8000/api/v1/departament/"
        }).then(response => {
        setDepartament(response.data)
        })
    }, [])

    const AddNewOwner = async (e) => {

        let formField = new FormData()

        formField.append('name', name)
        formField.append('surname', surname)
        formField.append('owner_description', owner_description)
        formField.append('departament', owner_departament)

    function readCookie(name) {
        let nameEQ = name + "=";
        let ca = document.cookie.split(';');
        for(let i=0;i < ca.length;i++) {
            let c = ca[i];
            while (c.charAt(0)===' ') c = c.substring(1,c.length);
            if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length,c.length);
        }
        return null;
    }

    let csrftoken = readCookie('csrftoken');

    if (surname && name) {
        await axios({
            headers: {"X-CSRFToken":csrftoken },
            method: 'post',
            url: 'http://127.0.0.1:8000/api/v1/owner/',
            data: formField
        }).then(response => {
            alert(`Owner ${surname} ${name} has been added`);
            e.preventDefault();
            })
        } else {
            alert('Surname & Name must be added');
        }
    }

    return (
        <div className="container sm">
            <form className="form-control p-2" onSubmit={AddNewOwner}>
                <input
                    type="text"
                    className="form-control my-2"
                    placeholder="Surname"
                    name="surname"
                    value={surname}
                    onChange={(e) => setSurname(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control mb-2"
                    placeholder="Name"
                    name="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    />
                <input
                    type="text"
                    className="form-control mb-2"
                    placeholder="Description"
                    name="owner_description"
                    value={owner_description}
                    onChange={(e) => setOwner_description(e.target.value)}
                    />
                <select className="form-select" name="departament" onSelect={(e) => setOwner_departament(e.target.value)}>
                  <option value="1">Open this select menu</option>
                  {departament.map(p => (
                  <option key={p.id} value={p.id}>{p.departament_title}</option>
                  ))}
                </select>
                <button type="submit" className="btn btn-secondary my-2">Submit</button>
            </form>
        </div>
    )
}
